.formulario-texto{
    p{
        font-size: 1.8rem;
        text-align: center;
        line-height: 1.8;
    }
}
.color-formulario{
    width: 90%;
    margin: 0 auto;
    background-color: $gris;
    border-radius: 1rem;
    margin-bottom: 2rem;

}
.formulario{
    padding: 3rem 0;
    label{
        font-size: 1.8rem;
        font-weight: $regular;
        display: block;
        color: $blanco;
    }
    input:not([type="submit"])
    ,textarea,select{
        border-radius: 0.5rem;
        padding: 1rem;
        display: block;
        width: 100%;
        border: none;
        margin-bottom: 1rem;
        
    }
    textarea{
        height: 20rem
    }
    @include tablet{
        @include grid(2,3rem);

        .mensaje-formulario,
        .boton-formulario{
            grid-column: 1/3;
        }
    }
    
}

.boton-formulario{
    display: block;
    margin: auto;
    margin-top: 2rem;

    @include tablet{
        display: inline-block;
        margin-top: 0;
    }
}